.top {
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #FFFFFF;
  border-bottom: 1px solid $gray;

  &__wrapper {
    display: flex;
    max-width: $maxWidth;
    margin: 0 auto;
    position: relative;
    min-width: 1760px;
    overflow: hidden;
  }

  &__first,
  &__second {
    width: 50%;
  }

  &__first {
    padding-top: $gap * 60;
    padding-bottom: $gap * 54;
    width: 100%;
    max-width: 820px;
    margin-right: 96px;
    position: relative;
    z-index: 1;
  }

  &__header {
    font-weight: 800;
    font-size: 80px;
    line-height: 100%;
    margin-bottom: $gap * 30;
  }

  &__paragraph {
    font-weight: 500;
    font-size: 26px;
    line-height: 120%;
    margin-bottom: $gap * 29;
    max-width: 675px;
  }

  &__link {
    margin-bottom: $gap * 20;
    position: relative;
    z-index: 20;

    i {
      position: absolute;
      display: inline-block;
      border-top: 13px solid #ffad01;
      border-left: 13px solid #ffad01;
      border-radius: 4px;
      width: 36px;
      height: 36px;
      top: -6px;
      left: 19.5%;
      transform: rotate(45deg);
      z-index: -1;
    }
  }

  &__link:hover,
  &__link:focus {
    i {
      border-top: 13px solid #FFC201;
      border-left: 13px solid #FFC201;
    }
  }

  &__second-number {
    position: absolute;
    left: 50.5%;
    bottom: 168px;
    transform: translate(-50%);
    z-index: 1;
  }

  &__list {
    @include flex(center, flex-start);

    flex-wrap: wrap;
  }

  &__list-item {
    margin-bottom: $gap * 16;

    a {
      border-bottom: 1px solid $gray;
      color: inherit;
      text-decoration: none;
    }
  }

  &__list-item,
  &__list-icon {
    @include flex(center, center);
  }

  &__list-item:nth-of-type(2n-1) {
    margin-right: $gap * 14;
  }

  &__list-item:last-of-type {
    br {
      display: none;
    }
  }

  &__list-icon {
    width: 70px;
    height: 70px;
    background: $white;
    box-shadow: 0px 10px 30px -6px rgba(35, 25, 4, 0.15);
    border-radius: 10px;
    margin-right: 20px;
  }

  &__list-text {
    max-width: 305px;

    a {
      position: relative;
      top: -4px;

      &:hover,
      &:focus {
        transition: all 0.4s;
        border-bottom: 1px solid $yellow;
      }
    }
  }

  &__second {
    position: absolute;
    top: -134px;
    right: -30px;
  }

  &__back {
    img {
      width: 976px;
      height: auto;
    }
  }

  .wrapper {
    overflow-x: visible;
  }
}

@media(max-width: $desktopA) {
  .top {
    &__first {
      max-width: 680px;
      padding-top: $gap * 41;
      padding-bottom: $gap * 40;
    }

    &__header {
      font-weight: 800;
      font-size: 70px;
      line-height: 70px;
    }

    &__paragraph {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: $gap * 30;
    }

    &__second {
      top: -134px;
      right: 182px;
    }

    &__second-number {
      left: 29%;
      bottom: 204px;
    }

    &__back {
      img {
        width: 796px;
        height: 950px;
      }
    }

    &__link {
      i {
        left: 23.6%;
      }
    }

    &__list-text {
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      max-width: 242px;
    }

    &__list-item {
      margin-bottom: $gap * 20;
    }

    &__list-item:nth-of-type(2n-1) {
      margin-right: $gap * 8;
    }

    &__list-item:last-of-type {
      br {
        display: inline-block;
      }
    }
  }
}

@media(max-width: $desktopB) {
  .top {
    &__header {
      font-size: 50px;
      line-height: 50px;
      max-width: 335px;
      margin-bottom: $gap * 20;
    }

    &__paragraph {
      font-size: 16px;
      line-height: 19px;
      max-width: 470px;
      margin-bottom: $gap * 20;
    }

    &__link {
      margin-bottom: $gap * 16;

      i {
        left: 17.4%;
      }
    }

    &__list-item {
      margin-bottom: $gap * 14;
    }

    &__list-item:nth-of-type(2n-1) {
      margin-right: $gap * 10;
    }

    &__list-item:nth-of-type(3) {
      svg, img {
        width: 22px;
        height: 16px;
      }
    }

    &__list-item:last-of-type  &__list-text {
      position: relative;
      top: 4px;
    }

    &__list-icon {
      width: 50px;
      height: 50px;
      margin-right: 16px;

      img {
        width: 36px;
        height: 36px;
      }
    }

    &__list-text {
      font-size: 14px;
      line-height: 20px;
      max-width: 186px;
    }

    &__back {
      img {
        width: 631px;
        height: 714px;
      }
    }

    &__second {
      top: -92px;
      right: 333px;
    }

    &__second-number {
      left: 20.8%;
      bottom: 127px;
    }

    &__first {
      padding-bottom: 57px;
    }
  }
}