.wrapper {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  max-width: $wrapperSize;
  box-sizing: border-box;
  padding: 0 20px;
  margin: 0 auto;
  position: relative;
}

@media(max-width: $desktopA) {
  .wrapper {
    padding: 0 143px;
  }   
}

@media(max-width: $desktopB) {
  .wrapper {
    padding: 0 87px;
  }
}
